export default {
  data() {
    return {
      isLoading: true,
      detail: {
        last_price: 0
      },
      id: '',
      doStart: false,
      startPrice: 0,
      incr_price: 0
    }
  },
  beforeCreate() {

    this.isLoading = true
    this.$http.get('/api/getgoodInfo?id=' + this.$route.params.id)
      .then((res) => {
        this.detail = res.data.data;


        document.title = this.detail.title
        this.isLoading = false
      })
      .catch((err) => {
        console.log(err)
      })




  },
  created() {},
  mounted() {

  },
  methods: {

  },
  computed: {
    isNum() {
      return parseInt(this.detail.incr_price);
    }
  },
  components: {

  }
}




